import {
  AlertDialog,
  AlertDialogAction,
  AlertDialogCancel,
  AlertDialogContent,
  AlertDialogDescription,
  AlertDialogFooter,
  AlertDialogHeader,
  AlertDialogTitle,
} from "@/components/ui/alert-dialog";
import { useTranslation } from "react-i18next";
import { ExternalLink } from "lucide-react";

interface ExternalLinkConfirmDialogProps {
  open: boolean;
  onOpenChange: (open: boolean) => void;
  onConfirm: () => void;
  siteName?: string;
  url?: string;
  title?: string;
  description?: string;
  confirmLabel?: string;
}

const ExternalLinkConfirmDialog = ({
  open,
  onOpenChange,
  onConfirm,
  siteName,
  url,
  title: customTitle,
  description: customDescription,
  confirmLabel,
}: ExternalLinkConfirmDialogProps) => {
  const { t } = useTranslation();

  // Extract domain from URL for display
  let displayDomain = siteName || "";
  if (url) {
    try {
      displayDomain = new URL(url).hostname;
    } catch {
      displayDomain = url;
    }
  }

  return (
    <AlertDialog open={open} onOpenChange={onOpenChange}>
      <AlertDialogContent className="max-w-[320px] w-[calc(100%-2rem)] mx-auto border-0 bg-white/80 dark:bg-gray-900/80 backdrop-blur-2xl shadow-2xl rounded-[28px] overflow-hidden p-6">
        {/* Glass effect overlay */}
        <div className="absolute inset-0 rounded-[28px] bg-gradient-to-b from-white/40 via-transparent to-transparent pointer-events-none" />
        
        <AlertDialogHeader className="relative text-center space-y-3">
          {/* Icon container */}
          <div 
            className="mx-auto w-14 h-14 rounded-2xl bg-gradient-to-br from-primary/20 to-primary/10 dark:from-primary/30 dark:to-primary/20 flex items-center justify-center shadow-sm shrink-0"
            style={{ transform: 'translateZ(0)', willChange: 'transform' }}
          >
            <ExternalLink className="w-7 h-7 text-primary shrink-0" strokeWidth={1.5} />
          </div>
          <div className="space-y-1.5 px-1">
            <AlertDialogTitle className="text-base font-semibold tracking-tight text-foreground">
              {customTitle || t("externalLink.confirmTitle")}
            </AlertDialogTitle>
            <AlertDialogDescription className="text-sm text-muted-foreground/80 leading-relaxed break-words">
              {customDescription || t("externalLink.confirmDescription", { site: displayDomain })}
            </AlertDialogDescription>
          </div>
        </AlertDialogHeader>
        
        <AlertDialogFooter className="relative flex-col sm:flex-col gap-2 pt-4 mt-1 sm:space-x-0">
          <AlertDialogAction 
            onClick={onConfirm} 
            className="w-full rounded-xl h-11 bg-primary hover:bg-primary/90 text-primary-foreground font-medium shadow-lg shadow-primary/20 transition-all duration-200"
          >
            {confirmLabel || t("externalLink.continue")}
          </AlertDialogAction>
          <AlertDialogCancel className="w-full rounded-xl h-11 bg-black/5 dark:bg-white/10 hover:bg-black/10 dark:hover:bg-white/15 border-0 text-muted-foreground font-medium transition-all duration-200 mt-0">
            {t("common.cancel")}
          </AlertDialogCancel>
        </AlertDialogFooter>
      </AlertDialogContent>
    </AlertDialog>
  );
};

export default ExternalLinkConfirmDialog;
